body {
    background: #3494E6;
    background: -webkit-linear-gradient(to right, #EC6EAD, #3494E6);
    background: linear-gradient(to right, #EC6EAD, #3494E6);
    font-family: 'Saira Condensed', monospace;
    height: 90vh;
}

button {
    font-family: monospace;
    margin: 10px;
    padding: 10px 15px;
    text-align: center;
    text-transform: uppercase;
    color: white;
    border-radius: 10px;
    font-size: 1rem;
    font-weight: bold;
}

button:hover {
    cursor: pointer;
    border: 1px solid white !important;
}

#settings-panel button {
    background-color: #0c77cc;
    border: 1px solid #1a1a1a;
    padding: 8px 10px;
}

#controls-panel button {
    background-color: #b242a4;
    border: 1px solid #1a1a1a;
    padding: 15px 25px;
}

#clock-container {
    text-align: center;
    background-color: black;
    width: 80vmin;
    margin: 10vh auto;
    padding: 16px;
    border-radius: 80px;
}

#display-container {
    background-image: linear-gradient(135deg, #aafff4 10%, #f8bcdd 100%);
    color: #121418;
    font-size: 60px;
    border-radius: 60px;
    border: 15px solid #0a0e15;
}

#controls-panel {
    padding: 5px;
    width: 40vh;
    margin: auto;
    color: white;
}

#settings-panel {
    padding: 10px 0;
    font-family: 'Saira Condensed', sans-serif;
    font-size: 1.2rem;
    background-color: #1d293b;
    border-radius: 30px;
    color: white;
}

#break-label {
    width: 40vmin;
    margin-right: 10px;
}

#break-length {
    font-size: 1.6rem;
    width: 50px;
    background-color: black;
    border: 1px solid #0c161f;
    border-radius: 10px;
    padding: 0 10px;
}

#session-label {
    width: 30vmin;
    margin-left: 10px;
}

#session-length {
    font-size: 1.6rem;
    width: 50px;
    background-color: black;
    border-radius: 10px;
    padding: 0 10px;
}

#timer-label {
    text-transform: uppercase;
    font-size: 2.7rem;
    height: 3rem;
}

#time-left {
    font-size: 6rem;
    font-weight: bold;
}
